* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
body {
  background: #fbf9fe;
  font: 14px PingFangSC-Light;
}
html,
body {
  height: 100%;
  width: 100%;
  overflow-x: hidden;
}
h3 {
  font-family: 'Lato', sans-serif;
  font-size: 16px;
  margin-bottom: 10px;
  font-weight: 600;
  color: #292929;
}
a {
  text-decoration: none;
}
.clear {
  clear: both;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  height: 100%;
}
@font-face {
  font-family: 'iconfont';
  src: url('../libs/iconfont/iconfont.eot');
  src: url('../libs/iconfont/iconfont.eot?#iefix') format('embedded-opentype'), url('../libs/iconfont/iconfont.woff') format('woff'), url('../libs/iconfont/iconfont.ttf') format('truetype'), url('../libs/iconfont/iconfont.svg#iconfont') format('svg');
}
.iconfont {
  font-family: "iconfont" !important;
  font-size: 16px;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: 0.2px;
  -moz-osx-font-smoothing: grayscale;
}
.view2 a.weui-form-preview__btn_default {
  color: #f9b0be;
}
.vux-slider > .vux-swiper > .vux-swiper-item > a > p.vux-swiper-desc {
  height: auto;
}
/*- home START -*/
.line {
  height: 1px;
  background-color: rgba(0, 0, 0, 0.1);
  margin-bottom: 10px;
  display: block;
  clear: both;
}
.hContent {
  margin-top: 15px;
  padding: 0 15px;
}
.hContent p {
  line-height: 20px;
  margin-bottom: 5px;
  font-size: 14px;
  letter-spacing: 0px;
}
.container {
  margin-bottom: 10px;
  position: relative;
  padding-bottom: 20px;
}
.bz {
  text-align: right;
  font-size: 13px;
  font-style: normal;
  padding-right: 10px;
  position: absolute;
  bottom: 2px;
  left: 0;
  width: 100%;
}
.bz span a {
  color: #225599;
}
.bz b {
  float: left;
  margin-left: 4px;
  position: relative;
}
.bz b > em {
  position: absolute;
  color: #f398aa;
  left: 10px;
  top: 0;
  opacity: 0;
}
.bz b > em.active {
  animation: addOneMove 2s ease-out;
  left: 10px;
  opacity: 0;
}
@keyframes addOneMove {
  from {
    left: 10px;
    opacity: 1;
  }
  to {
    left: 30px;
    opacity: 0;
  }
}
.bz em {
  margin-left: 10px;
  font-style: normal;
  color: #797979;
}
.Hicon {
  float: left;
  font-size: 16px;
  margin-top: 1px;
}
.Hicon:nth-child(2) {
  color: #f9b0be;
}
.Hitem {
  display: flex;
}
.Hitem img {
  width: 65px;
  height: 65px;
  border-radius: 65px;
}
.Hitem .mr {
  margin-right: 20px;
}
.Hitem .ml {
  margin-left: 20px;
}
.Hshort {
  flex: 1;
}
/*- home END -*/
/*- my START -*/
.my .header {
  width: 100%;
  height: 55vw;
  border-bottom: 1px solid #eae8ed;
  position: relative;
  overflow: hidden;
}
.my .bg_blur {
  z-index: -2;
  position: absolute;
  top: -20%;
  left: -20%;
  width: 140%;
  height: 140%;
  min-height: 100%;
  display: block;
  max-height: none;
  animation: blur 2s linear;
}
@keyframes blur {
  from {
    opacity: 0.1;
  }
  to {
    opacity: 1;
  }
}
.my .center {
  text-align: center;
  padding-top: 7vw;
  color: #fff;
  font-size: 18px;
}
.my .center img {
  width: 25vw;
  height: 25vw;
  border-radius: 50%;
  border: 2px solid #ececec;
}
.my .center .headPic {
  position: absolute;
  top: 7vw;
  left: 50%;
  opacity: 0;
  width: 25vw;
  height: 25vw;
  margin-left: -12.5vw;
}
.my .name {
  text-align: center;
  font-size: 0.9em;
  color: #000;
  display: block;
}
.my .friend {
  text-align: center;
  line-height: 2.2em;
  font-size: 1.1em;
  color: #e7e7e7;
}
.my .friend > span {
  margin: 0 8px;
}
.my .friend b {
  margin-left: 3px;
  font-weight: normal;
}
.my .women {
  color: #c2185b;
  font-size: 1em;
}
.my .man {
  color: #273bc4;
  font-size: 1.3em;
  vertical-align: middle;
}
/*- my END -*/



